import React, {useEffect, useRef} from "react";
import {useHistory, useLocation} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import LinkIcon from "@material-ui/icons/Link";

export function WikiHeading(props: { level: number, children: any, node: any }) {
    const myRef: React.MutableRefObject<any> = useRef()
    const location = useLocation()
    const history = useHistory()
    
    const headingHash = props.node.myHeadingHash;
    
    useEffect(() => {
        if (location.hash) {
            // console.log(headingHash)
            if (location.hash === headingHash) {
                myRef.current.scrollIntoView({behavior: 'smooth'});
            }
        }
    },[headingHash, location.hash])
    
    return <div id={headingHash} style={{
        display: "flex", flexDirection: "row", alignItems: "stretch"
    }}>
        <div style={{
            display: "inline-flex", width: 5, background: "#ed4b82",
            marginTop: 2, marginBottom: 2, marginRight: 10,
            flexShrink: 0
        }}/>
        <Typography ref={myRef} style={{fontSize: 40 - props.level * 5}}>
            {props.children}
            <IconButton onClick={() => {
                history.push(headingHash)
            }}>
                <LinkIcon/>
            </IconButton>
        </Typography>
    </div>;
}