import React, {useEffect, useRef, useState} from "react";
import {MyBanner} from "../MyBanner";
import {CircularProgress, List} from "@material-ui/core";
import ReactMarkdown from "react-markdown";
import {
    getIsVerticalMobile,
    getPublicResourcePath,
    getStringAfter,
    myMarkdownRenderers,
    useEndPoint
} from "../../my_util/Util";
import {useTranslation} from "react-i18next";
import {useLocation} from "react-router-dom";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Divider from "@material-ui/core/Divider";
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from "@material-ui/core/Drawer";
import {useMutatedMarkdown} from "./MutatedReactMarkdown";
import {MyLinkInWiki, sidebarMarkdownRenderers, WikiToSideBarContext} from "./WikiSidebar";
import {WikiHeading} from "./WikiHeading";
import _ from "lodash/fp";

const gfm = require('remark-gfm')

//both github wiki url and this website url
function getMdUrlFromWikiUrl(url: string) {
    const head = 'https://raw.githubusercontent.com/wiki/qouteall/ImmersivePortalsMod'
    
    var innerPath = getStringAfter(url, '/wiki');
    
    if (innerPath.length === 0) {
        return head + '/Home.md';
    }
    
    return head + innerPath + '.md';
}

const wikiMarkdownRenderers = {
    ...myMarkdownRenderers,
    table: (a: any) => {
        return <Table>{a.children}</Table>
    },
    tableHead: (a: any) => <TableHead>{a.children}</TableHead>,
    tableRow: (a: any) => <TableRow>{a.children}</TableRow>,
    tableBody: (a: any) => <TableBody>{a.children}</TableBody>,
    tableCell: (a: any) => <TableCell>{a.children}</TableCell>,
    link: (a: any) => {
        return <MyLinkInWiki targetLink={a.href}>{a.children}</MyLinkInWiki>
    },
    heading: (a: any) => {
        return <WikiHeading level={a.level} node={a.node}>{a.children}</WikiHeading>
    }
}

export function IPWikiPage() {
    let {t, i18n} = useTranslation();
    
    let location = useLocation();
    
    const sidebarUrl = t('url_wiki_sidebar');
    const {data: sideBarData, complete: complete} = useEndPoint(sidebarUrl);
    
    const wikiContentUrl = getMdUrlFromWikiUrl(location.pathname);
    
    const {data: wikiData, complete: wikiResultComplete} = useEndPoint(wikiContentUrl);
    
    const [wikiTitle, setWikiTitle] = useState('Loading...');
    
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    
    const leftBarComponent = <WikiToSideBarContext.Provider value={{
        setTitle: setWikiTitle,
        onTermClicked: () => setIsDrawerOpen(false)
    }}>
        <Typography variant='h5' style={{margin: 10, marginBottom: 0, marginLeft: 15}}>
            Pages
        </Typography>
        <Divider style={{margin: 10}}/>
        
        <List component='nav' style={{maxWidth: '80vw'}}>
            {
                complete ?
                    <ReactMarkdown renderers={sidebarMarkdownRenderers}>
                        {sideBarData || 'error loading ' + sidebarUrl}
                    </ReactMarkdown>
                    : <CircularProgress/>
            }
        </List>
    </WikiToSideBarContext.Provider>;
    
    const leftBarOnMobile = <>
        <Drawer anchor='left' open={isDrawerOpen} onClose={
            () => setIsDrawerOpen(false)
        } style={{}}>
            {leftBarComponent}
        </Drawer>
        
        {/*render to make the effect trigger and set the wiki title.
         It's a workaround and will be refactored*/}
        {!isDrawerOpen && <div style={{display: "none"}}>
            {leftBarComponent}
        </div>}
    </>;
    
    const leftBarOnPC = <div style={{
        // position: 'sticky', top: 0,
        minWidth: '20vw', maxWidth: '20vw'
    }} className='My-Sticky'>{leftBarComponent}</div>;
    
    const scrollDomRef = useRef(null);
    
    var {
        wikiContentComponent: wikiContentComponent,
        rightBarComponent: rightBarComponent,
        updateScrollFunc: updateScrollFunc
    } = useMutatedMarkdown(
        // @ts-ignore
        wikiContentUrl, wikiMarkdownRenderers, [gfm], scrollDomRef
    );
    
    const realScrollCallback = useRef(null);
    
    useEffect(() => {
        const debouncedFunc = _.throttle(166, updateScrollFunc);
        
        // @ts-ignore
        realScrollCallback.current = debouncedFunc;
        
        return () => {
            debouncedFunc.cancel();
        }
    }, [realScrollCallback]);
    
    const isVerticalMobile = getIsVerticalMobile();
    return <div className='My-Page' style={{
        display: 'flex', flexDirection: 'column'
    }} onScroll={() => {
        if (realScrollCallback.current) {
            // @ts-ignore
            realScrollCallback.current();
        }
    }} ref={scrollDomRef}>
        <MyBanner text={'Immersive Portals Wiki'} color='#9500ae' sticky={true}
                  pageIconPath={getPublicResourcePath('/immptl.png')}>
            {isVerticalMobile &&
            <IconButton onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
                <MenuIcon style={{color: 'white'}}/>
            </IconButton>}
        </MyBanner>
        
        <div style={{
            display: 'flex', flexDirection: 'row', alignItems: "flex-start"
        }}>
            {isVerticalMobile ? leftBarOnMobile : leftBarOnPC}
            
            <div style={{
                margin: 0, padding: 20, flexGrow: 1,
                minWidth: 0, flexBasis: 0
            }}>
                <Typography variant='h4'>{wikiTitle}</Typography>
                <Divider style={{margin: 10}}/>
                {wikiContentComponent}
            </div>
            
            {!isVerticalMobile && <div style={{
                margin: 0, minWidth: '20vw', maxWidth: '20vw',
                // position: 'sticky', top: 0,
                maxHeight: '100vh', overflowY: 'auto',
            }} className='My-Sticky'>
                <Typography variant='h5' style={{margin: 10, marginBottom: 0}}>
                    Contents
                </Typography>
                <Divider style={{margin: 10}}/>
                {rightBarComponent}
            </div>}
        
        
        </div>
    
    </div>;
}