import React, {useRef} from 'react';
import {useHistory} from "react-router-dom";
import {MyBanner} from "./MyBanner";

import Card from "@material-ui/core/Card";
import {useTranslation} from "react-i18next";
import {getIsVerticalMobile, getPublicResourcePath, MyButton, MyRemoteMarkdown} from "../my_util/Util";
import {IPDownload} from "./IPDownload";
import {Typography} from "@material-ui/core";

export function IPFeatures(props: {}) {
    let {t, i18n} = useTranslation();
    
    const path = t('url_ip_features');
    
    return <MyRemoteMarkdown url={path}/>
    
}

export function IPPage(props: {}) {
    let history = useHistory();
    
    let {t, i18n} = useTranslation();
    
    const path = t('url_ip');
    
    // const {data: resultData, complete: resultComplete} = useEndPoint(path);
    
    const downloadSectionRef = useRef(null)
    
    const executeScroll = () => {
        // @ts-ignore
        downloadSectionRef.current.scrollIntoView({behavior: 'smooth'});
    }
    
    const spacing = getIsVerticalMobile() ? 15 : 30;
    
    return (
        <div className='My-Page'>
            <MyBanner text='Immersive Portals' color='#9500ae' pageIconPath={getPublicResourcePath('/immptl.png')}/>
            
            <div style={{margin: spacing, display: "flex", flexDirection: "column", justifyContent: "flex-start"}}>
                <Card style={{
                    display: 'flex', flexDirection: 'row',
                    justifyContent: 'flex-start', alignItems: 'center',
                    flexWrap: "wrap",
                    minHeight: '40vh',
                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                    marginBottom: spacing
                }} square={false} elevation={10}>
                    
                    <img src={getPublicResourcePath('/immptl.png')} alt='i am alt' style={{
                        flexGrow: 0,
                        width: 150,
                        margin: 40,
                        marginRight: 0,
                        marginBottom: getIsVerticalMobile() ? 0 : 40
                    }}/>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'space-between',
                        alignSelf: 'stretch',
                        margin: 40,
                        // marginLeft: 0,
                        marginBottom: 20,
                        flexGrow: 1, flexBasis: 0, minWidth: '50vw'
                    }}>
                        
                        <MyRemoteMarkdown url={path}/>
                        
                        <div style={{flexDirection: 'row', justifyContent: 'flex-start'}}>
                            <MyButton style={{backgroundColor: '#35baf6', margin: 10}}
                                      onClick={executeScroll}
                            >
                                {t('ip_download')}
                            </MyButton>
                            
                            <MyButton style={{backgroundColor: '#35baf6', margin: 10}} onClick={() => {
                                history.push('/immptl/wiki')
                            }}>
                                {t('ip_wiki')}
                            </MyButton>
                        
                        </div>
                    
                    </div>
                </Card>
                
                
                <Card style={{
                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                    padding: 20,
                    marginBottom: spacing
                }} elevation={10}>
                    <Typography variant='h6'>{t('Features')}</Typography>
                    <IPFeatures/>
                </Card>
                
                <Card style={{
                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                    padding: 20,
                    marginBottom: spacing
                }} elevation={10} ref={downloadSectionRef}>
                    <Typography variant='h5'>{t('Downloads (Fabric version)')}</Typography>
                    <Typography variant='body1'>{t(
                        'Only the latest minecraft version of this mod is being maintained.'
                    )}</Typography>
                    <IPDownload/>
                </Card>
            
            </div>
        
        
        </div>
    );
}